.portfolio {
  background: white;
}

.portfolio-header {
  text-align: center;
  margin-bottom: 3rem;
}

.portfolio-subtitle {
  font-size: 1.5rem;
  color: #667eea;
  font-weight: 600;
  margin-bottom: 1rem;
}

.portfolio-filter {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-bottom: 3rem;
  flex-wrap: wrap;
}

.filter-btn {
  background: transparent;
  border: 2px solid #e2e8f0;
  color: #666;
  padding: 0.8rem 1.5rem;
  border-radius: 25px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
}

.filter-btn:hover,
.filter-btn.active {
  background: #667eea;
  color: white;
  border-color: #667eea;
}

.portfolio-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 2rem;
  margin-bottom: 4rem;
}

.portfolio-card {
  background: white;
  border-radius: 20px;
  overflow: hidden;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
  position: relative;
}

.portfolio-card:hover {
  transform: translateY(-5px);
}

.portfolio-card.featured {
  border: 2px solid #667eea;
}

.featured-badge {
  position: absolute;
  top: 1rem;
  right: 1rem;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  padding: 0.3rem 0.8rem;
  border-radius: 20px;
  font-size: 0.8rem;
  font-weight: 600;
  z-index: 2;
}

.portfolio-image {
  position: relative;
  overflow: hidden;
}

.portfolio-image img {
  width: 100%;
  height: 250px;
  object-fit: cover;
  transition: transform 0.3s ease;
}

.portfolio-card:hover .portfolio-image img {
  transform: scale(1.05);
}

.portfolio-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(102, 126, 234, 0.9);
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.portfolio-card:hover .portfolio-overlay {
  opacity: 1;
}

.portfolio-links {
  display: flex;
  gap: 1rem;
}

.portfolio-link {
  width: 50px;
  height: 50px;
  background: white;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #667eea;
  text-decoration: none;
  transition: transform 0.3s ease;
}

.portfolio-link:hover {
  transform: scale(1.1);
}

.portfolio-content {
  padding: 1.5rem;
}

.portfolio-meta {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}

.portfolio-category {
  background: linear-gradient(135deg, #f1f5f9 0%, #e2e8f0 100%);
  color: #667eea;
  padding: 0.3rem 0.8rem;
  border-radius: 20px;
  font-size: 0.8rem;
  font-weight: 600;
}

.portfolio-year {
  color: #666;
  font-weight: 500;
}

.portfolio-title {
  font-size: 1.3rem;
  font-weight: 600;
  color: #1a1a1a;
  margin-bottom: 0.8rem;
}

.portfolio-description {
  color: #666;
  line-height: 1.6;
  margin-bottom: 1rem;
}

.portfolio-tech {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  margin-bottom: 1rem;
}

.tech-tag {
  background: #f8fafc;
  color: #667eea;
  padding: 0.2rem 0.6rem;
  border-radius: 15px;
  font-size: 0.7rem;
  font-weight: 500;
  border: 1px solid rgba(102, 126, 234, 0.2);
}

.portfolio-stats {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1rem;
  padding-top: 1rem;
  border-top: 1px solid #e2e8f0;
}

.stat {
  text-align: center;
}

.stat-value {
  display: block;
  font-size: 1.2rem;
  font-weight: 700;
  color: #667eea;
  margin-bottom: 0.2rem;
}

.stat-label {
  font-size: 0.8rem;
  color: #666;
}

.graphic-design-section {
  margin-top: 4rem;
  text-align: center;
}

.graphic-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 1rem;
  margin: 2rem 0;
}

.graphic-card {
  position: relative;
  border-radius: 15px;
  overflow: hidden;
  aspect-ratio: 1;
}

.graphic-card img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s ease;
}

.graphic-card:hover img {
  transform: scale(1.05);
}

.graphic-overlay {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background: linear-gradient(transparent, rgba(0, 0, 0, 0.8));
  color: white;
  padding: 1rem;
  transform: translateY(100%);
  transition: transform 0.3s ease;
}

.graphic-card:hover .graphic-overlay {
  transform: translateY(0);
}

.graphic-overlay h4 {
  font-size: 1rem;
  font-weight: 600;
  margin-bottom: 0.2rem;
}

.graphic-overlay p {
  font-size: 0.8rem;
  opacity: 0.8;
}

.portfolio-cta {
  background: linear-gradient(135deg, #f8fafc 0%, #e2e8f0 100%);
  padding: 3rem 2rem;
  border-radius: 20px;
  margin-top: 2rem;
}

.portfolio-cta h3 {
  font-size: 2rem;
  font-weight: 700;
  color: #1a1a1a;
  margin-bottom: 1rem;
}

.portfolio-cta p {
  font-size: 1.1rem;
  color: #666;
  margin-bottom: 2rem;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}

@media (max-width: 768px) {
  .portfolio-grid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }
  
  .portfolio-filter {
    gap: 0.5rem;
  }
  
  .filter-btn {
    padding: 0.6rem 1rem;
    font-size: 0.9rem;
  }
  
  .graphic-grid {
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  }
  
  .portfolio-stats {
    grid-template-columns: 1fr;
    gap: 0.5rem;
  }
  
  .portfolio-cta {
    padding: 2rem 1rem;
  }
  
  .portfolio-cta h3 {
    font-size: 1.5rem;
  }
}
